@import url("https://fonts.googleapis.com/css2?family=Bungee&family=Noto+Sans&display=swap");
:root {
  --bg-color: #343434;
  --color-dark: #1c1c1c;
}

header {
  display: flex;
  justify-content: space-around;
}
header #header-icon {
  width: 200px;
}
header #header-title {
  background: #9100ff;
  background: linear-gradient(90deg, #9100ff 0%, #090979 50%, #00d4ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 4em;
  text-align: center;
}

.card {
  height: 600px;
  width: 400px;
  margin: 0 15px 50px 15px;
  background-image: url(../images/landscape.png);
  background-size: cover;
  background-position: center;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.41);
  overflow: hidden;
}
.card:nth-child(1) h2 {
  background: #002bff;
  background: linear-gradient(90deg, #002bff 0%, #2ebfff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(2) h2 {
  background: #00ffc2;
  background: linear-gradient(90deg, #00ffc2 0%, #47f9ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(3) h2 {
  background: #a600ff;
  background: linear-gradient(90deg, #a600ff 0%, #4747ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(4) h2 {
  background: #0052ff;
  background: linear-gradient(90deg, #0052ff 0%, #00ff84 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(5) h2 {
  background: #002bff;
  background: linear-gradient(90deg, #002bff 0%, #2ebfff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(6) h2 {
  background: #00ffc2;
  background: linear-gradient(90deg, #00ffc2 0%, #47f9ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(7) h2 {
  background: #a600ff;
  background: linear-gradient(90deg, #a600ff 0%, #4747ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card:nth-child(8) h2 {
  background: #0052ff;
  background: linear-gradient(90deg, #0052ff 0%, #00ff84 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card .card-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(16px);
}
.card .card-wrapper .card-header {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  background-color: #1c1c1c;
  text-align: center;
}
.card .card-wrapper .card-body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}
.card .card-wrapper .card-body a,
.card .card-wrapper .card-body button {
  position: absolute;
  bottom: 30px;
  z-index: 1;
  align-self: center;
  width: 100px;
}
.card .card-wrapper .card-body a:hover,
.card .card-wrapper .card-body button:hover {
  background-color: #eaeaea;
  color: #68a832;
}
.card .card-wrapper .card-body .card-content {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgba(234, 234, 234, 0.3);
  color: #1c1c1c;
  text-align: center;
  line-height: 30px;
  transform: translateY(600px);
  transition: 200ms;
}
.card .card-wrapper .card-body .card-content .material-icons {
  font-size: 2.5em;
}
.card .card-wrapper .card-body:hover .card-content {
  transform: translateY(0);
}

body {
  background: var(--bg-color);
  color: #eaeaea;
  overflow: hidden;
}

.wrapper {
  height: 100vh;
  overflow-y: auto;
}

.simplebar-scroll-content {
  box-sizing: content-box;
}

main {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

h1 {
  font-family: "Bungee", sans-serif;
}

h2 {
  font-family: "Noto Sans", sans-serif;
  letter-spacing: 15px;
}

p {
  font-family: "Noto Sans", sans-serif;
  letter-spacing: 3px;
}

button {
  padding: 5px 20px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  border: 7px solid #eaeaea;
  font-family: "Noto Sans", sans-serif;
  color: #eaeaea;
  font-size: 1.3em;
  transition: 200ms;
}